<template>
  <div class="load-status"> 
    <div class="load-status-box">    
      <div :class="props.site_esd?'':'esd'">{{lzn.lbl_siteESD}}</div>
      <div :class="props.bay_esd?'':'esd'">{{lzn.lbl_bayESD}}</div>
      <div :class="(props.bay_earth&&props.bay_overfill)?'':'esd'">{{lzn.lbl_earth_overfill+' '+((props.bay_earth&&props.bay_overfill)?lzn.lbl_connected:lzn.lbl_not_connected)}}</div>
      <div :class="props.bay_vapour?'':'esd'">{{lzn.lbl_vapour+' '+(props.bay_vapour?lzn.lbl_connected:lzn.lbl_not_connected)}}</div>
    </div>
    <div style="padding:8px;">{{lzn.lbl_curBayCode}}: {{baycode}}</div>
    <div style="padding:8px;padding-top:0;">{{lzn.lbl_loadStat}}: {{ls}}</div>
    <img class="flexlinc" src="../assets/FlexLinc.png" alt="FlexLinc">
  </div>
</template>

<script>
export default {
  name: 'loadstatus',
  props: {
    props: {
      type: Object,
      default:function(){
        return {};
      }
    },
    ls:String,
    baycode:String,
    lzn:Object
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss">
  .load-status{
    width:500px;
    position:relative;
    .esd{
      background-color:red;
    }
  }
  .load-status-box{
    background:linear-gradient(to bottom,#a6a6a6, #d6d6d6);
    padding:5px;
    border:1px solid #4a4a4a;
    box-shadow:1px 1px 2px rgba(0,0,0,.4);
    width:320px;
    &>div{
      white-space:nowrap;
      border:1px solid #000;
      margin-top:10px;
      background-color:#00b050;
      padding:2px 4px;
      &:last-child{
        margin-bottom:10px;
      }
    }
  }
  .flexlinc{
    position:absolute;
    right:0;
    top:20px;
    width:120px;
    height:154px;
  }
</style>



