<template>
  <div class="bay-detail">
    <div style="padding-top:15px;" class="container-fluid">
      <div class="row clearfix">
        <div class="col-sm-6"><load-status :lzn="lzn.bayDetails" :ls="bay_load.load_status" :baycode="$route.params.bdid" :props="bay_interlocks"></load-status></div>
        <div class="col-sm-6"><load-detail :lzn="lzn.bayDetails" :props="bay_load" style="float:right;"></load-detail></div>
      </div>
    </div>
    <div style="padding:0 15px;position:relative;">
      <div v-show="pnlist&&Object.keys(pnlist).length>0" class="bd-bg">
        <div>{{lzn.bayDetails.lbl_cmptNo}}</div>
        <div>{{lzn.bayDetails.lbl_prodName}}</div>
      </div>
      <div style="position:relative;z-index:5;">
        <PipeNode v-for="(item, key) in pnlist" :key="key" :props="item"></PipeNode>
      </div>
    </div>
    <div class="ft">
      <sbutton @click.native="print"><span>{{lzn.bayDetails.btn_prtScn}}</span></sbutton><sbutton @click.native="showTD"><span>{{lzn.bayDetails.btn_tnkrDetails}}</span></sbutton><sbutton @click.native="showO"><span>{{lzn.bayDetails.btn_overview}}</span></sbutton>
    </div>
  </div>
</template>

<script>
import Sbutton from './Sbutton'
import LoadDetail from './LoadDetail'
import LoadStatus from './LoadStatus'
import PipeNode from './PipeNode'
import html2canvas from 'html2canvas'

export default {
  name: 'baydetail',
  props: {
    lzn:Object
  },
  components: {
    Sbutton,LoadDetail,LoadStatus,PipeNode
  },
  data () {
    return {
      st: null,
      stpn:null,
      bay_load:{},
      bay_interlocks:{},
      pipeNodeList:[]
    }
  },
  created(){
    document.title = this.lzn.bayDetails.title;
    this.getRTBayItem();
    this.getPipeNodeList();
  },
  computed:{
    pnlist(){
      let t={};
      this.pipeNodeList.forEach((n)=>{
        if(!t[n.arm_code]){
          t[n.arm_code] = [];
        }
        t[n.arm_code].push(n);
      });
      return t;
    }
  },
  methods:{
    showO(){
      this.$router.push({ path: '/overview' });
    },
    showTD(){
       this.$router.back();
    },
    print(){
      html2canvas(document.body).then((canvas)=> {
        this.$emit('ps',canvas,'BayDetail');
      })
    },
    getRTBayItem(){
      this.$http.get(bayViewConfig.apis.bayItemRT+'&bay_code='+this.$route.params.bdid).then((r)=>{
        if(r.data){
          this.bay_load = r.data[0].bay_load[0];
          this.bay_interlocks = r.data[0].bay_interlocks;
        }
      }).finally(()=>{
          this.st = setTimeout(()=>{
            this.getRTBayItem();
          },bayViewConfig.refreshFrequency);
      });
    },
    getPipeNodeList(){
      this.$http.get(bayViewConfig.apis.pipenodeList+'&bay_code='+this.$route.params.bdid).then((r)=>{
        if(r.data){
          this.pipeNodeList = r.data;
          this.getRTPipeNodeList();
        }
      });
    },
    getRTPipeNodeList(){
      this.$http.get(bayViewConfig.apis.pipenodeListRT+'&bay_code='+this.$route.params.bdid).then((r)=>{
        if(r.data){
          for(let i=0;i<this.pipeNodeList.length;i++){
            Object.assign(this.pipeNodeList[i], r.data[i]);          
          }
        }
      }).finally(()=>{
          this.stpn = setTimeout(()=>{
            this.getRTPipeNodeList();
          },bayViewConfig.refreshFrequency);
      });
    }
  },
  beforeDestroy(){
    clearTimeout(this.st);
    clearTimeout(this.stpn);
  }
}
</script>

<style lang="scss">
.bay-detail{
  background-color:#ffffff;
  min-width:860px;
  padding-bottom:70px;
}
.bd-bg{
  position:absolute;
  right:15px;
  top:0;
  width:330px;
  height:100%;
  border:1px solid #000000;
  background:linear-gradient(to bottom,#a6a6a6, #d6d6d6);
  z-index:1;
  div{
    float:left;
    padding-left:30px;
    padding-top:5px;
  }
}
</style>



