<template>
    <div class="cmpt-item">   
      <div class="cmpt-process" :style="{height:process+'%','background-color':props.prod_color}">
        
      </div>
      <div class="cmpt-data">
        <div>{{props.cmpt_no}}</div>
        <div>{{props.qty_preset}}</div>
        <div>{{props.qty_obs}}</div>
        <div>{{props.prod_name}}</div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'cmpt',
  props: {
    props: Object
  },
  data () {
    return {

    }
  },
  computed:{
    process(){
      return Math.round(this.props.qty_obs/this.props.qty_preset*10000)/100;
    }
  },
  methods:{

  }
}
</script>

<style lang="scss">
  .cmpt-item{
    width:84px;
    box-sizing:border-box;
    background:linear-gradient(to top,#979797, #a4a4a4);
    position:relative;
  }
  .cmpt-process{
    position:absolute;
    bottom:0;
    width:100%;
    z-index:1;
  }
  .cmpt-data{
    border:1px solid #fff;
    border-top:none;
    position:relative;
    z-index:2;
    div{
      white-space:nowrap;
      height:26px;
      line-height:26px;
      padding:0 2px;
      border-top:1px solid #fff;
    }
  }
</style>



