<template>
  <div id="app">
    <router-view :lzn="applzn" @ps="print"></router-view>
    <div v-show="psWrapShow" @click.self="hide" class="ps-wrap">
      <a :href="imgDataUrl" :download="fn+'.png'">
        <img style="vertical-align:middle;width:980px;" :src="imgDataUrl" alt="" title="点击保存">
      </a>
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',
  data(){
    return {
        imgDataUrl:'',
        psWrapShow:false,
        fn:'',
        applzn:{
          "overview": {
              "title": "BayView - Overview",
              "select_bayType": "Loading Bay Type",
              "select_bayCode": "Loading Bay Code",
              "select_baseProd": "Base Product",
              "btn_expData": "Export Data",
              "btn_prtScn": "Print Screen",
              "lbl_esd": "EMERGENCY SHUT DOWN",
              "lbl_tnkrID": "TANKER ID",
              "lbl_loadID": "LOAD ID"
          },
          "tankerDetails": {
              "title": "BayView - Tanker Details",
              "btn_prtScn": "Print Screen",
              "btn_bayDetails": "Bay Details",
              "btn_overview": "BayView Overview",
              "lbl_siteESD": "SITE EMERGENCY STOP",
              "lbl_bayESD": "BAY EMERGENCY STOP",
              "lbl_earth_overfill": "EARTH \/ OVERFILL",
              "lbl_vapour": "VAPOUR",
              "lbl_connected": "CONNECTED",
              "lbl_not_connected": "NOT CONNECTED",
              "lbl_curBayCode":"Bay Code",
              "lbl_loadStat": "Load Status",
              "lbl_loadDetails": "LOAD DETAILS",
              "lbl_cmpyCode": "Company Code",
              "lbl_loadNo": "Load Number",
              "lbl_operName": "Operator Name",
              "lbl_tnkrCode": "Tanker Code",
              "lbl_cmptNo": "Cmpt No.",
              "lbl_preset": "Preset",
              "lbl_qty_obs": "Observed Qty",
              "lbl_prodName": "Product Name",
              "lbl_armNo": "Arm Number",
              "lbl_temp": "Temperature"
          },
          "bayDetails": {
              "title": "BayView - Bay Details",
              "btn_prtScn": "Print Screen",
              "btn_tnkrDetails": "Tanker Details",
              "btn_overview": "BayView Overview",
              "lbl_siteESD": "SITE EMERGENCY STOP",
              "lbl_bayESD": "BAY EMERGENCY STOP",
              "lbl_earth_overfill": "EARTH \/ OVERFILL",
              "lbl_vapour": "VAPOUR",
              "lbl_connected": "CONNECTED",
              "lbl_not_connected": "NOT CONNECTED",
              "lbl_curBayCode":"Bay Code",
              "lbl_loadStat": "Load Status",
              "lbl_loadDetails": "LOAD DETAILS",
              "lbl_cmpyCode": "Company Code",
              "lbl_loadNo": "Load Number",
              "lbl_operName": "Operator Name",
              "lbl_tnkrCode": "Tanker Code",
              "lbl_tnkrDetails": "TANKER DETAILS",
              "lbl_armNo": "Arm No.",
              "lbl_cmptNo": "Cmpt. No.",
              "lbl_prodName": "Product Name"
          }
      }
    }
  },
  created(){
    this.getViewTxt();
  },
  methods:{
    print(canvas,fname){
      this.imgDataUrl = canvas.toDataURL("image/png");
      this.fn = fname;
      this.psWrapShow = true;
    },
    hide(){
      this.psWrapShow = false;
    },
    getViewTxt(){
      this.$http.get(bayViewConfig.apis.viewtxt).then((r)=>{
        if(r.data){
          this.applzn = r.data;
        }
      });
    }
  }
}
</script>

<style lang="scss">
html{
  height:100%;
}
body {
  -webkit-font-smoothing: antialiased;
  background-color:#ffffff;
  height:100%;
}
.clearfix{
  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}
.ft{
  position:fixed;
  width:100%;
  left:0;
  z-index:999;
  bottom:0;
  padding:10px 0 20px 0;
  background:#ffffff;
  text-align:right;
  button{
    margin-right:10px;
  }
}
.fl{
  float:left;
}
.ps-wrap{
  position: fixed;
  z-index: 99999;
  display: block;
  top: 0px;
  left: 0px;
  background-color: #000;
  text-align: center;
  width: 100%;
  height: 100%;
  line-height:974px;
}
</style>



